.info {
  display: flex;
  position: relative;
  @include desktop-hover() {
    svg {
      fill: #01231f;
    }
    .info__hint {
      opacity: 1;
      visibility: visible;
    }
  }
  svg {
    fill: #80918f;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  &__hint {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: calc(100% + 8px);
    display: flex;
    width: 320px;
    border: none;
    box-shadow: none;
    padding: 0;
    padding: 15px;
    font-size: 14px;
    line-height: 1.4;
    white-space: normal;
    border-radius: 4px;
    font-size: 14px;
    line-height: 1.4;
    white-space: normal;
    outline: 0;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    transition-property: visibility, opacity;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      width: 250px;
    }
    p {
      margin: 0;
    }
    .info.active & {
      opacity: 1;
      visibility: visible;
    }
    &::before {
      content: "";
      position: absolute;
      right: 100%;
      top: 50%;
      margin-top: -6px;
      border: 6px solid transparent;
      border-right: 4px solid var(--Primary-Main, #01231f);
    }
    &--bottom {
      width: 260px;
      left: 50%;
      margin-left: -130px;
      top: calc(100% + 8px);
      transform: none;
      @include response(1200px) {
        left: auto;
        right: calc(100% + 6px);
        margin-left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
      &::before {
        content: "";
        position: absolute;
        border-right: 6px solid transparent;
        border-bottom: 4px solid var(--Primary-Main, #01231f);
        bottom: 100%;
        left: 50%;
        margin-left: -6px;
        right: auto;
        top: auto;
        margin-top: 0;
        @include response(1200px) {
          border: 6px solid transparent;
          border-left: 4px solid var(--Primary-Main, #01231f);
          left: 100%;
          top: 50%;
          margin-top: -6px;
          margin-left: 0;
          bottom: auto;
        }
      }
    }
  }
}
